<template>
  <div id="main-content" class="container">
    <div class="row" style="max-height: 30%; overflow-y: auto;">
      <div class="col-md-12">
        <h3 align="center">Welcome to Jack's Private Chat Room</h3>
        <form>
          <div class="form-group">
            <b-row>
              <b-col cols="6">
                <label for="name">Your Name:</label>
                <b-input
                  type="text"
                  id="name"
                  class="form-control ml-1"
                  v-model="name"
                  :state="nameState"
                  placeholder="Please enter your name here..."
                />
                <b-form-invalid-feedback id="name-feedback">
                  Enter at least 3 letters
                </b-form-invalid-feedback>
              </b-col>
              <b-col cols="6">
                <b-button
                  block
                  id="connect"
                  size="sm"
                  variant="outline-primary"
                  class="badge-default ml-1 mr-1"
                  type="submit"
                  :disabled="connected == true || nameState == false"
                  @click.prevent="connect"
                  >Join</b-button
                >
                <b-button
                  block
                  id="disconnect"
                  variant="outline-primary"
                  size="sm"
                  class="badge-default ml-1 mr-1"
                  type="submit"
                  :disabled="connected == false"
                  @click.prevent="disconnect"
                >
                  Leave
                </b-button>
              </b-col>
            </b-row>
          </div>
        </form>
      </div>
    </div>

    <div class="row chatroom" v-chat-scroll>
      <ul class="messages">
        <li class="message" v-for="(item, index) in received_messages" :key="index"><span v-html="item" /></li>
      </ul>
    </div>

    <div class="row mt-2 mb-2" style="height: 5%">
      <div class="col-md-12">
        <form>
          <div class="form-group">
            <b-row no-gutters> 
              <b-col cols="9">
                <input
                  type="text"
                  id="message"
                  ref="message"
                  class="form-control"
                  v-model="send_message"
                  :disabled="connected == false"
                  placeholder="Please enter your message here..."
                />
              </b-col>
              <b-col cols="3">
                <b-button 
                  id="send" size="md" block
                  variant="outline-primary"
                  class="badge-default ml-1"
                  :disabled="connected == false"
                  type="submit"
                  @click.prevent="send"
                >
                  Send
                </b-button>
              </b-col>
            </b-row>
          </div>
        </form>
      </div>
    </div>

    </div>
</template>

<script>
import SockJS from "sockjs-client";
import Stomp from "webstomp-client";
import config from "../myconfig.js";

export default {
  name: "websocketdemo",
  data() {
    return {
      name: "",
      received_messages: [],
      send_message: "",
      connected: false,
    };
  },
  computed: {
    nameState() {
      return this.name.length >= 3 ? true : false;
    },
  },
  methods: {
    send() {
      if (this.send_message.length > 0) {
        console.log("Send message:" + this.send_message);
        if (this.stompClient && this.stompClient.connected) {
          const msg = { name: this.name, message: this.send_message };
          this.stompClient.send("/app/hello", JSON.stringify(msg), {});
          this.send_message = "";
        }
      }
    },
    connect() {
      this.socket = new SockJS(config.websocketURL);
      this.stompClient = Stomp.over(this.socket);
      this.stompClient.connect(
        {},
        (frame) => {
          this.connected = true;
          console.log(frame);
          this.stompClient.subscribe("/topic/messages", (msg) => {
            console.log(msg);
            //this.received_messages.push(JSON.parse(tick.body).content);

            let timestamp = JSON.parse(msg.body).timestamp;
            let userName = JSON.parse(msg.body).name;
            let message = JSON.parse(msg.body).message;

            let msgDate = new Date(timestamp);
            //let dateStr = msgDate.getHours() + ":" + msgDate.getMinutes() + ":" + msgDate.getSeconds();
            let dateStr = msgDate.toLocaleString([], {hour: '2-digit', minute: '2-digit', second: '2-digit'});

            if (userName) {
              message = "<b>" + userName + "</b>: " + message;
            }

            // add date
            message = dateStr + ": " + message;
            this.received_messages.push(message);
          });

          this.stompClient.send(
            "/app/hello",
            JSON.stringify({ name: this.name, message: null }),
            {}
          );
        },
        (error) => {
          console.log(error);
          this.connected = false;
        }
      );

      this.$refs.message.focus();
    },
    disconnect() {
      if (this.stompClient) {
        this.stompClient.disconnect();
      }
      this.connected = false;
    },
    tickleConnection() {
      this.connected ? this.disconnect() : this.connect();
    },
  },
  mounted() {
    // this.connect();
  },
};
</script>

<style scoped>
  .container {
    height: 100%;
  }
  .chatroom {
    min-height: 40%;
    max-height: 50%;
    border: 1px solid black;
    padding: 10px 1px;
    overflow-y: auto;
    margin-right: 0.5px;
    margin-left: 0.5px;
  }
  .message {
    list-style: none;
  }
</style>
